import { Component, OnInit, ViewChild, ElementRef, AfterViewInit } from '@angular/core';


@Component({
  selector: 'app-docs',
  templateUrl: './docs.component.html',
  styleUrls: ['./docs.component.css']
})

export class DocsComponent implements OnInit {
  @ViewChild('sprintPlaning') public canvas: ElementRef;
  context: CanvasRenderingContext2D;
  constructor() { }

  epicObj = [
    {
      epic: 1,
      stories: [
        {
          id: 's1',
          jd: 100,
          pred: []
        },
        {
          id: 's2',
          jd: 80,
          pred: []
        },
        {
          id: 's3',
          jd: 20,
          pred: []
        }
      ],
      xy: [300, 20]
    },
    {
      epic: 2,
      stories: [
        {
          id: 's4',
          jd: 20,
          pred: []
        },
        {
          id: 's5',
          jd: 0,
          pred: ['s4']
        },
        {
          id: 's6',
          jd: 0,
          pred: ['s5']
        }
      ],
      xy: [50, 160]
    },
    {
      epic: 3,
      stories: [
        {
          id: 's7',
          jd: 0,
          pred: ['s3','s6']
        },
        {
          id: 's8',
          jd: 0,
          pred: ['s7']
        },
        {
          id: 's9',
          jd: 0,
          pred: []
        }
      ],
      xy: [600, 200]
    }
  ];
  ngAfterViewInit() {
    
    let sp_canvas = this.canvas.nativeElement;
    this.context = sp_canvas.getContext("2d");
    // console.log('epicPlaning ', sp_canvas);
    // for (var i = 0; i < 4; i++) {
    //   let x = (170 * (i + 1)) + 20;
    //   let percentage = 10 * (i + 1);
    //   this.story(x, 20, 150, 80, percentage);
    //   console.log('x ', x);
    // }
    console.log('this.epicObj ', this.epicObj);
    for (var i = 0; i < this.epicObj.length; i++) {
      let epicW = ((100 + 20) * this.epicObj[i].stories.length) + 40;
      let epicH = (50 + 20 + 40)
      this.context.strokeRect(this.epicObj[i].xy[0], this.epicObj[i].xy[1], epicW, epicH);
      for (var j = 0; j < this.epicObj[i].stories.length; j++) {
        let x = this.epicObj[i].xy[0] + (120 * j) + 30;
        let y = this.epicObj[i].xy[1] + 30;
        let completePercentage = this.epicObj[i].stories[j].jd;
        this.story(x, y, 100, 50, completePercentage);
        this.storyTitle(this.epicObj[i].stories[j].id , x+50, y+25);
        if (this.epicObj[i].stories[j].pred.length > 0) {
          /**
           * Concept or Logic
           * 1. First will check the predecessor story id, 
           * 2. Find epic index of that story and the index of the story itself
           * 3. will calculate the postion of the parent epic and a position of the dependend story's position, width height
           * 4. compare dependents xy with dependecies 
           * 5. draw the line after getting the position coordinates
           */
          for (var k = 0; k < this.epicObj[i].stories[j].pred.length; k++) {
            
            let sId = this.epicObj[i].stories[j].pred[k];
            let epicIndex = i;
            let storyIndex = j;
            let thisX = x;
            let thisY = y;
            // ---------- trying to get index by using map function ---------- result = 0
            // let depStoryIndex = this.epicObj.map(function(x){return x.stories.map(function(y){ return y.id}).indexOf(sId)});
            console.log('sid ', sId);
            /**
             * Searching for the epic[index] && story[index]
             */
            for(var ei = 0; ei<this.epicObj.length; ei++ ){
              for(var si = 0; si<this.epicObj[ei].stories.length; si++){
                if(this.epicObj[ei].stories[si].id.indexOf(sId) !== -1){
                  let depX = this.epicObj[ei].xy[0] + (120 * si) + 30; // getting dependend X
                  let depY = this.epicObj[ei].xy[1] + 30; // getting dependend y
                  
                  this.predcessorLine(thisX, thisY, depX, depY);
                }
              }
            }
          }
        }
      }
    }

  }
  epic(x, y, w, h) {
    this.context.strokeRect(x, y, w, h);
  }
  story(x, y, w, h, percentage) {
    // this.context.clearRect(0,0,1100,500);
    this.context.fillStyle = 'green';
    this.context.fillRect(x, y, percentage, h);
    this.context.strokeRect(x, y, w, h);
  }

  predcessorLine(tx, ty, dx, dy) {
    /**
     * tx, ty: this object XY , which is dependent on dxdy object
     * dx, dy: dependend object XY , which is not depndend to txty object
     * 
     */
    let mx, my, lx, ly;
    if(tx>dx) { // dxdy ------- txty
      mx = dx + 100; 
      lx = tx;
    }
    if(tx<dx){ // txty ------- dxdy
      mx = tx + 100; 
      lx = dx;
    }
    my = dy + 25;
    ly = ty + 25;

    this.context.beginPath();
    this.context.moveTo(mx, my);
    this.context.lineTo(lx, ly);
    this.context.stroke();
  }

  storyTitle(s,x,y){
    this.context.fillStyle = 'black';
    this.context.font = "15px Arial";
    this.context.fillText(s,x-6,y+6);
  }



  ngOnInit() {

  }

}
