import { NgModule }             from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
 
import { HomeComponent }   from '../app/components/home/home.component';
import { ChartComponent }   from '../app/components/chart/chart.component';
import { DocsComponent } from '../app/components/docs/docs.component';
import { ShortcutsComponent } from '../app/components/shortcuts/shortcuts.component';
import { PagenotfoundComponent } from '../app/components/pagenotfound/pagenotfound.component';
 
const routes: Routes = [
  { path: '', redirectTo: '/welcome', pathMatch: 'full' },
  { path: 'welcome', component: HomeComponent },
  { path: 'graph', component: ChartComponent },
  { path: 'documentation', component: DocsComponent },
  { path: 'shortcuts', component: ShortcutsComponent },
  {path:'**', component:PagenotfoundComponent}
];
 
@NgModule({
  imports: [ RouterModule.forRoot(routes) ],
  exports: [ RouterModule ]
})
export class AppRouterModule { }
